@import '../../base';

%standard-font {
  font-family: $font-open-sans;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  text-align: center;
  color: white;
}
$heavy-weight: 700;
$horizontal-anim-duration: 90s;
$vertical-anim-duration: 4.5s;

.security {
  position: relative;
  overflow: hidden;

  .title-container {
    max-width: 1000px;
    text-align: center;
    margin: auto;
    padding: 20px;

    @media screen and(min-width: 820px){
      padding: 40px;
    }

    .title {
      @extend %standard-font;
      display: inline;
      font-size: $font-size-big;
      font-weight: $heavy-weight;
      color: $color-title !important;

      @media screen and(min-width: 820px){
        font-size: $font-size-extra-big;
      }
    }

    .desc {
      text-align: center;
      @extend %standard-font;
      font-size: $font-size-small;
      font-weight: $font-weight-normal;
      color: $color-title !important;

      @media screen and (min-width: 1440px) {
        font-size: 19.5;
      }
    }
  }
  .content {

    .grey-bg {
        background-color: #f2f7f8;
        padding: 50px 0 !important;
      }
    
    .content-row {
      justify-content: center;
      margin-bottom: 50px;
      padding: 20px;
  
      @media screen and(min-width: 768px){
        padding: 40px;
      }

      .icon {
        display: block;

        @media screen and (min-width: 820px) {
          display: flex;
        }

        .icon-container {
          padding: 40px 50px;
          
          .desc {
            @extend %standard-font;
            font-size: $font-size-small;
            font-weight: $font-weight-bold;
            color: $color-title;
            text-align: left !important;
            padding: 10px 0;
            margin: auto;
            line-height: 24px;
            text-align: center !important;
        
            @media screen and (min-width: 1440px) {
              font-size: $font-size-regular;
            }
          }
          .sub-desc {
            @extend %standard-font;
            font-size: $font-size-smallest;
            color: $color-body;
            text-align: left !important;
            padding: 10px 0;
            margin: auto;
            text-align: center !important;
            line-height: 24px;
        
            @media screen and (min-width: 1440px) {
              font-size: $font-size-small;
            }
          }
  
          .animate {
            cursor: pointer;
            -webkit-transition: -webkit-transform ease-out 0.1s, background 0.2s;
            -moz-transition: -moz-transform ease-out 0.1s, background 0.2s;
            transition: transform ease-out 0.1s, background 0.2s;

            &:after {
              top: 0;
              left: 0;
              padding: 0;
              z-index: -1;
              box-shadow: 0 0 0 2px rgba(0,217,175,0.1);
              opacity: 0;
              -webkit-transform: scale(0.9);
              -moz-transform: scale(0.9);
              -ms-transform: scale(0.9);
              transform: scale(0.9);
            }
            
            &:hover {
              background: rgba(0,217,175,0.0);
              -webkit-transform: scale(0.93);
              -moz-transform: scale(0.93);
              -ms-transform: scale(0.93);
              transform: scale(0.93);
              color: #fff;
            }
            
            &:hover:after {
              -webkit-animation: sonarEffect 1.3s ease-out 75ms;
              -moz-animation: sonarEffect 1.3s ease-out 75ms;
              animation: sonarEffect 1.3s ease-out 75ms;
            }

            img {
              width: 100px;
              padding: 10px;
              cursor: pointer;
            }
          }

          // sonar animation
          @-webkit-keyframes sonarEffect {
            0% {
              opacity: 0.1;
            }
            40% {
              opacity: 0.4;
              box-shadow: 0 0 0 2px rgba(0,217,175,0.1), 0 0 10px 10px #fff, 0 0 0 10px rgba(0,217,175,0.5);
            }
            100% {
              box-shadow: 0 0 0 2px rgba(0,217,255,0.1), 0 0 10px 10px #fff, 0 0 0 10px rgba(0,217,175,0.5);
              -webkit-transform: scale(1.5);
              opacity: 0;
            }
          }
          @-moz-keyframes sonarEffect {
            0% {
              opacity: 0.1;
            }
            40% {
              opacity: 0.4;
              box-shadow: 0 0 0 2px rgba(0,217,175,0.1), 0 0 10px 10px #fff, 0 0 0 10px rgba(0,217,175,0.5);
            }
            100% {
              box-shadow: 0 0 0 2px rgba(0,217,175,0.1), 0 0 10px 10px #fff, 0 0 0 10px rgba(0,217,175,0.5);
              -moz-transform: scale(1.5);
              opacity: 0;
            }
          }
          @keyframes sonarEffect {
            0% {
              opacity: 0.1;
            }
            40% {
              opacity: 0.4;
              box-shadow: 0 0 0 2px rgba(0,217,175,0.1), 0 0 10px 10px #fff, 0 0 0 10px rgba(0,217,175,0.5);
            }
            100% {
              box-shadow: 0 0 0 2px rgba(0,217,175,0.1), 0 0 10px 10px #fff, 0 0 0 10px rgba(0,217,175,0.5);
              transform: scale(1.5);
              opacity: 0;
            }
          }
        }
      }
    }
  
    .title {
      @extend %standard-font;
      font-size: $font-size-big;
      font-weight: $font-weight-bold;
      color: $color-title;
      text-align: center !important;
      width: 80vw;
      margin: auto;
  
      @media screen and(min-width: 820px){
        font-size: $font-size-extra-big;
      }
    }
  
    .title-desc {
      @extend %standard-font;
      font-size: $font-size-smallest;
      color: $color-body;
      text-align: left !important;
      padding: 10px 0;
      width: 80vw;
      margin: auto;
      line-height: 24px;
  
      @media screen and (min-width: 1440px) {
        font-size: $font-size-small;
      }
  
   
    }
  
    ol {
      padding-left: 15px;
      padding-top: 10px;
  
      li {
        padding: 5px 0;
      }
    }
  }
}